import './Board.css';
import Row from 'components/Row/Row';
import useKeydownListener from 'utils/useKeydownListener';
import * as MODE from 'cellModes';
import { useEffect, useState } from 'react';
import WORDS from 'utils/words';
import Keyboard from 'components/Keyboard/Keyboard';
import { useCookies } from 'react-cookie';

const initializeEmptyLetter = (value = '') => {
	return {
		value,
		mode: MODE.EMPTY,
	};
};

const initializeEmptyRow = (letterCount) => {
	return Array.from({ length: letterCount }, () => initializeEmptyLetter());
};

const initializeEmptyBoard = (guessCount, letterCount) => {
	return Array.from({ length: guessCount }, () =>
		initializeEmptyRow(letterCount)
	);
};

const Board = ({ target, guessCount, letterCount, onRestart }) => {
	const [cookies, setCookie] = useCookies();

	const [board, setBoard] = useState(
		initializeEmptyBoard(guessCount, letterCount)
	);

	const [row, setRow] = useState(0);

	const [gameOver, setGameOver] = useState(false);

	const [submitted, setSubmitted] = useState([]);

	const [triggerShake, setTriggerShake] = useState(false);

	const handler = ({ key }) => {
		if (!gameOver) {
			if (key === 'Backspace' || key === 'Delete') {
				deleteLetter();
			} else if (key === 'Enter') {
				submitWord();
			} else if (key.match(/^[a-zA-Z]{1}$/)) {
				addLetter(key.toUpperCase());
			}
		}
	};

	useKeydownListener(handler);

	const deleteLetter = () => {
		const letters = board[row];

		let index = -1;
		for (let letter of letters) {
			if (letter.value !== '') {
				index += 1;
			} else {
				break;
			}
		}

		if (index !== -1) {
			const newLetters = [
				...letters.slice(0, index),
				initializeEmptyLetter(),
				...letters.slice(index + 1),
			];
			setBoard([
				...board.slice(0, row),
				newLetters,
				...board.slice(row + 1),
			]);
		}
	};

	const addLetter = (value) => {
		const letters = board[row];

		let index = 0;
		for (let letter of letters) {
			if (letter.value === '') {
				break;
			} else {
				index += 1;
			}
		}

		if (index < letters.length) {
			const newLetters = [
				...letters.slice(0, index),
				{ value, mode: MODE.UNSUBMITTED },
				...letters.slice(index + 1),
			];
			setBoard([
				...board.slice(0, row),
				newLetters,
				...board.slice(row + 1),
			]);
		}
	};

	const submitWord = () => {
		const letters = board[row];

		if (letters.every((letter) => letter.value !== '')) {
			const word = letters.map((letter) => letter.value).join('');
			if (WORDS.filter((w) => w.toUpperCase() === word).length > 0) {
				const newLetters = [];
				let numCorrect = 0;
				for (const [index, letter] of letters.entries()) {
					const char = letter.value;
					if (char === target[index]) {
						newLetters.push({ ...letter, mode: MODE.CORRECT });
						numCorrect += 1;
					} else if (target.indexOf(char) === -1) {
						newLetters.push({ ...letter, mode: MODE.WRONG });
					} else {
						const freq = target.filter((c) => c === char).length;
						const used =
							letters
								.slice(0, index)
								.filter((c) => c.value === char).length +
							letters
								.slice(index + 1)
								.filter(
									(c, i) =>
										c.value === target[index + 1 + i] &&
										c.value === char
								).length;
						if (used < freq) {
							newLetters.push({
								...letter,
								mode: MODE.MISPLACED,
							});
						} else {
							newLetters.push({ ...letter, mode: MODE.WRONG });
						}
					}
				}
				setBoard([
					...board.slice(0, row),
					newLetters,
					...board.slice(row + 1),
				]);
				setSubmitted([
					...submitted,
					newLetters.map((letter) => letter.value),
				]);
				if (numCorrect === newLetters.length) {
					setGameOver(true);
					let newStats = [...cookies.stats];
					newStats[row] += 1;
					let newCurrentStreak =
						Number.parseInt(cookies.currentStreak) + 1;
					let newMaxStreak = Math.max(
						newCurrentStreak,
						cookies.maxStreak
					);
					setCookie('stats', newStats);
					setCookie('currentStreak', newCurrentStreak);
					setCookie('maxStreak', newMaxStreak);
				} else if (row === board.length - 1) {
					setGameOver(true);
					setRow(row + 1);
					let newStats = [...cookies.stats];
					newStats[board.length] += 1;
					setCookie('stats', newStats);
					setCookie('currentStreak', 0);
				} else {
					setRow(row + 1);
				}
			} else {
				setTriggerShake(true);
			}
		} else {
			setTriggerShake(true);
		}
	};

	useEffect(() => {
		if (triggerShake) {
			setTimeout(() => setTriggerShake(false), 1000);
		}
	}, [triggerShake]);

	return (
		<>
			<div className="board">
				{board.map((letters, key) => (
					<Row
						key={key}
						letters={letters}
						selected={row === key}
						shake={row === key && triggerShake}
					/>
				))}
				{gameOver && row === board.length ? (
					<>
						<div style={{ height: '20px' }}></div>
						<Row
							letters={target.map((letter) => ({
								value: letter,
								mode: MODE.CORRECT,
							}))}
						/>
					</>
				) : (
					<></>
				)}
			</div>
			{gameOver ? (
				<div
					style={{
						width: 'fit-content',
						margin: '0 auto',
						paddingTop: '25px',
					}}>
					<div
						className="wordle-button wordle-restart"
						onClick={onRestart}>
						RESTART
					</div>
				</div>
			) : (
				<Keyboard
					target={target}
					submitted={submitted}
					handler={handler}
				/>
			)}
		</>
	);
};

export default Board;
