import './Key.css';
import * as MODE from 'cellModes';

const classNameLookup = {
	[MODE.WRONG]: 'wordle-wrong-key',
	[MODE.MISPLACED]: 'wordle-misplaced-key',
	[MODE.CORRECT]: 'wordle-correct-key',
};

const Key = ({ letter, onClick, mode }) => {
	return (
		<div
			className={
				mode
					? classNameLookup[mode] + ' wordle-button wordle-key'
					: 'wordle-button wordle-key'
			}
			onClick={onClick}>
			{letter}
		</div>
	);
};

export default Key;
