import { useEffect } from 'react';
import { useCookies } from 'react-cookie';
import './Statistics.css';

const sum = (array) => {
	return array.reduce((subtotal, value) => subtotal + value, 0);
};

const getWidth = (value, outOf) => {
	const percent = Math.floor((100 * value) / outOf);
	if (percent === 0) {
		return 'fit-content';
	} else {
		return percent.toString() + '%';
	}
};

const Statistics = ({ toGame }) => {
	const [cookies, setCookie] = useCookies();

	useEffect(() => {
		if (!cookies.stats) {
			setCookie('stats', [0, 0, 0, 0, 0, 0, 0]);
		}
		if (!cookies.currentStreak) {
			setCookie('currentStreak', 0);
		}
		if (!cookies.maxStreak) {
			setCookie('maxStreak', 0);
		}
	}, []);

	const { stats, currentStreak, maxStreak } = cookies;
	const guesses = stats ? stats.slice(0, -1) : [0, 0, 0, 0, 0, 0];

	const numPlayed = stats ? sum(stats) : 0;
	const numWon = sum(guesses);

	const largest = Math.max(...guesses);

	return (
		<div className="stats-container">
			<div className="stats-header">
				<div className="stats-title">STATISTICS</div>
				<div className="close-icon" onClick={toGame}>
					<svg
						xmlns="http://www.w3.org/2000/svg"
						height="24"
						viewBox="0 0 24 24"
						width="24">
						<path
							fill="var(--color-tone-3)"
							d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"></path>
					</svg>
				</div>
			</div>
			<div className="summary-container">
				<div className="summary-item">
					<div className="summary-value">{numPlayed}</div>
					<div className="summary-label">Played</div>
				</div>
				<div className="summary-item">
					<div className="summary-value">
						{numPlayed !== 0
							? Math.round((100 * numWon) / numPlayed)
							: '-'}
					</div>
					<div className="summary-label">Win %</div>
				</div>
				<div className="summary-item">
					<div className="summary-value">{currentStreak}</div>
					<div className="summary-label">Current Streak</div>
				</div>
				<div className="summary-item">
					<div className="summary-value">{maxStreak}</div>
					<div className="summary-label">Max Streak</div>
				</div>
			</div>
			<p className="guess-count-title">GUESS DISTRIBUTION</p>
			{guesses.map((value, n) => (
				<div key={n} className="guess-row">
					<div className="guess-label">{n + 1}</div>
					<div
						className="guess-bar"
						style={{ width: getWidth(value, largest) }}>
						<div className="guess-bar-value">{guesses[n]}</div>
					</div>
				</div>
			))}
		</div>
	);
};

export default Statistics;
