import './Cell.css';
import * as MODE from 'cellModes';

const classNameLookup = {
	[MODE.EMPTY]: 'wordle-empty-cell',
	[MODE.UNSUBMITTED]: 'wordle-unsubmitted-cell',
	[MODE.WRONG]: 'wordle-wrong-cell',
	[MODE.MISPLACED]: 'wordle-misplaced-cell',
	[MODE.CORRECT]: 'wordle-correct-cell',
};

const Cell = ({ letter, mode, active }) => {
	let className = 'wordle-cell ' + classNameLookup[mode];
	className += active ? ' wordle-active-cell' : '';

	return <div className={className}>{letter ? letter : ''}</div>;
};

export default Cell;
