import Cell from 'components/Cell/Cell';
import './Row.css';

const isActive = (letters, i) => {
	if (i === 0 && letters[0].value === '') {
		return true;
	} else if (
		i !== 0 &&
		letters[i].value === '' &&
		letters[i - 1].value !== ''
	) {
		return true;
	} else {
		return false;
	}
};

const Row = ({ letters, selected, shake }) => {
	return (
		<div className={shake ? 'wordle-row shake-animation' : 'wordle-row'}>
			{letters.map((state, i) => (
				<Cell
					key={i}
					letter={state.value}
					mode={state.mode}
					active={selected && isActive(letters, i)}
				/>
			))}
		</div>
	);
};

export default Row;
