import './Settings.css';

const Settings = ({
	toGame,
	darkMode,
	toggleDarkMode,
	colorblindMode,
	toggleColorblindMode,
}) => {
	return (
		<div className="wordle-settings">
			<div className="settings-header">
				<div className="settings-title">SETTINGS</div>
				<div className="close-icon" onClick={toGame}>
					<svg
						xmlns="http://www.w3.org/2000/svg"
						height="24"
						viewBox="0 0 24 24"
						width="24">
						<path
							fill="var(--color-tone-3)"
							d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"></path>
					</svg>
				</div>
			</div>

			<div className="section">
				<div className="section-text">
					<div className="section-name">Dark Theme</div>
				</div>
				<div>
					<div
						className={darkMode ? 'switch switch-active' : 'switch'}
						onClick={toggleDarkMode}>
						<span
							className={
								darkMode ? 'knob knob-active' : 'knob'
							}></span>
					</div>
				</div>
			</div>

			<div className="section">
				<div className="section-text">
					<div className="section-name">Color Blind Mode</div>
					<div className="section-description">
						High contrast colors
					</div>
				</div>
				<div>
					<div
						className={
							colorblindMode ? 'switch switch-active' : 'switch'
						}
						onClick={toggleColorblindMode}>
						<span
							className={
								colorblindMode ? 'knob knob-active' : 'knob'
							}></span>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Settings;
