import Board from 'components/Board/Board';
import { useState } from 'react';
import WORDS from 'utils/words';
import './Game.css';
import _ from 'underscore';

const sessionWords = _.shuffle(WORDS);

const getTarget = (index) => {
	return sessionWords[index].toUpperCase().split('');
};

const Game = () => {
	const [gameId, setGameId] = useState(0);
	const [target, setTarget] = useState(getTarget(0));

	const onRestart = () => {
		setTarget(getTarget(gameId + 1));
		setGameId(gameId + 1);
	};

	const guessCount = 6;
	const letterCount = 5;

	return (
		<>
			<div className="wordle-game-container" id="game-container">
				<Board
					key={gameId}
					target={target}
					guessCount={guessCount}
					letterCount={letterCount}
					onRestart={onRestart}
				/>
			</div>
		</>
	);
};

export default Game;
