import Row from 'components/Row/Row';
import './Info.css';
import * as MODES from 'cellModes';

const Info = ({ toGame }) => {
	return (
		<div className="wordle-info">
			<div className="info-header">
				<div className="info-title">HOW TO PLAY</div>
				<div className="close-icon" onClick={toGame}>
					<svg
						xmlns="http://www.w3.org/2000/svg"
						height="24"
						viewBox="0 0 24 24"
						width="24">
						<path
							fill="var(--color-tone-3)"
							d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"></path>
					</svg>
				</div>
			</div>
			<div className="info-body">
				<div className="info-section">
					<div className="info-section-text">
						<p>
							Guess the <strong>WORDLE</strong> in 6 tries.
						</p>
						<p>
							Each guess must be a valid 5 letter word. Hit the
							enter button to submit.
						</p>
						<p>
							After each guess, the color of the tiles will change
							to show how close your guess was to the word.
						</p>
					</div>
				</div>
				<div className="info-section">
					<div className="info-section-text">
						<p>
							<strong>Examples</strong>
						</p>
						<Row
							letters={[
								{ value: 'W', mode: MODES.CORRECT },
								{ value: 'E', mode: MODES.UNSUBMITTED },
								{ value: 'A', mode: MODES.UNSUBMITTED },
								{ value: 'R', mode: MODES.UNSUBMITTED },
								{ value: 'Y', mode: MODES.UNSUBMITTED },
							]}
						/>
						<p>
							The letter <strong>W</strong> is in the word and in
							the correct spot.
						</p>
						<Row
							letters={[
								{ value: 'P', mode: MODES.UNSUBMITTED },
								{ value: 'I', mode: MODES.MISPLACED },
								{ value: 'L', mode: MODES.UNSUBMITTED },
								{ value: 'L', mode: MODES.UNSUBMITTED },
								{ value: 'S', mode: MODES.UNSUBMITTED },
							]}
						/>
						<p>
							The letter <strong>I</strong> is in the word but in
							the wrong spot.
						</p>
						<Row
							letters={[
								{ value: 'V', mode: MODES.UNSUBMITTED },
								{ value: 'A', mode: MODES.UNSUBMITTED },
								{ value: 'G', mode: MODES.UNSUBMITTED },
								{ value: 'U', mode: MODES.UNSUBMITTED },
								{ value: 'E', mode: MODES.WRONG },
							]}
						/>
						<p>
							The letter <strong>U</strong> is not in the word in
							any spot.
						</p>
					</div>
				</div>
				<div className="info-section-text">
					<p>
						This page has been created purely for learning purposes.
						The original <strong>WORDLE</strong> can be found at{' '}
						<a href="https://www.powerlanguage.co.uk/wordle/">
							powerlanguage.co.uk/wordle/
						</a>
					</p>
				</div>
			</div>
		</div>
	);
};

export default Info;
