import Key from 'components/Key/Key';
import './Keyboard.css';
import * as MODES from 'cellModes';

const Keyboard = ({ target, submitted, handler }) => {
	const firstRow = ['Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P'];
	const secondRow = ['A', 'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L'];
	const thirdRow = ['Z', 'X', 'C', 'V', 'B', 'N', 'M'];

	let usedLetters = {};

	for (const word of submitted) {
		for (const letter of word) {
			if (target.indexOf(letter) === -1) {
				usedLetters = { ...usedLetters, [letter]: MODES.WRONG };
			}
		}
	}

	for (const word of submitted) {
		for (const [index, letter] of word.entries()) {
			if (target[index] === letter) {
				usedLetters = { ...usedLetters, [letter]: MODES.CORRECT };
			}
		}
	}

	for (const word of submitted) {
		for (const letter of word) {
			if (
				usedLetters[letter] != MODES.CORRECT &&
				target.indexOf(letter) !== -1
			) {
				usedLetters = { ...usedLetters, [letter]: MODES.MISPLACED };
			}
		}
	}

	return (
		<div className="wordle-keyboard">
			<div className="wordle-keyboard-row">
				{firstRow.map((letter, index) => (
					<Key
						mode={usedLetters[letter]}
						key={index}
						letter={letter}
						onClick={() => handler({ key: letter })}
					/>
				))}
			</div>
			<div className="wordle-keyboard-row">
				<div style={{ flex: '0.5', display: 'block' }}></div>
				{secondRow.map((letter, index) => (
					<Key
						mode={usedLetters[letter]}
						key={index}
						letter={letter}
						onClick={() => handler({ key: letter })}
					/>
				))}
				<div style={{ flex: '0.5', display: 'block' }}></div>
			</div>
			<div className="wordle-keyboard-last-row">
				<div
					className="wordle-wide-key wordle-button "
					onClick={() => handler({ key: 'Enter' })}>
					ENTER
				</div>
				{thirdRow.map((letter, index) => (
					<Key
						mode={usedLetters[letter]}
						key={index}
						letter={letter}
						onClick={() => handler({ key: letter })}
					/>
				))}
				<div
					className="wordle-wide-key wordle-button"
					onClick={() => handler({ key: 'Delete' })}>
					DEL
				</div>
			</div>
		</div>
	);
};

export default Keyboard;
