import './App.css';
import Game from 'components/Game/Game';
import { useEffect, useState } from 'react';
import Header from 'components/Header/Header';
import Settings from 'components/Settings/Settings';
import Hideable from 'utils/Hideable';
import Info from 'components/Info/Info';
import { useCookies } from 'react-cookie';
import Statistics from 'components/Statistics/Statistics';

const App = () => {
	const [cookies, setCookie, removeCookie] = useCookies();

	const [colorblindMode, setColorblindMode] = useState(
		cookies.colorblindMode ? true : false
	);
	const [darkMode, setDarkMode] = useState(cookies.darkMode ? true : false);
	const [pageDisplay, setPageDisplay] = useState('GAME');

	useEffect(() => {
		if (!cookies.stats) {
			setCookie('stats', [0, 0, 0, 0, 0, 0, 0]);
		}
		if (!cookies.currentStreak) {
			setCookie('currentStreak', 0);
		}
		if (!cookies.maxStreak) {
			setCookie('maxStreak', 0);
		}
	}, []);

	useEffect(() => {
		if (darkMode) {
			setCookie('darkMode', true);
		} else if (cookies.darkMode) {
			removeCookie('darkMode');
		}
		if (colorblindMode) {
			setCookie('colorblindMode', true);
		} else if (cookies.colorblindMode) {
			removeCookie('colorblindMode');
		}
	}, [darkMode, colorblindMode]);

	useEffect(() => {
		const AppDiv = document.getElementById('App');
		const bgColor =
			getComputedStyle(AppDiv).getPropertyValue('--color-background');
		const html = document.getElementById('HTML');
		html.style.backgroundColor = bgColor;
	}, [darkMode]);

	const onToggleColorblindMode = () => {
		setColorblindMode(!colorblindMode);
	};

	const onToggleDarkMode = () => {
		setDarkMode(!darkMode);
	};

	const toSettings = () => {
		setPageDisplay('SETTINGS');
	};

	const toInfo = () => {
		setPageDisplay('INFO');
	};

	const toGame = () => {
		setPageDisplay('GAME');
	};

	const toStats = () => {
		setPageDisplay('STATS');
	};

	let AppClassName = darkMode ? 'App dark-theme ' : 'App light-theme ';
	if (colorblindMode) {
		AppClassName += 'colorblind-theme';
	}

	return (
		<div id="App" className={AppClassName}>
			<Header toSettings={toSettings} toInfo={toInfo} toStats={toStats} />
			<Game />
			<Hideable layer={1} show={pageDisplay === 'SETTINGS'}>
				<Settings
					toGame={toGame}
					darkMode={darkMode}
					toggleDarkMode={onToggleDarkMode}
					colorblindMode={colorblindMode}
					toggleColorblindMode={onToggleColorblindMode}
				/>
			</Hideable>
			<Hideable layer={2} show={pageDisplay === 'INFO'}>
				<Info toGame={toGame} />
			</Hideable>
			<Hideable layer={3} show={pageDisplay === 'STATS'}>
				<Statistics toGame={toGame} />
			</Hideable>
		</div>
	);
};

export default App;
